
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import { msgFormatter, validatePhone, validateQQ } from '../../utils'
import { mapActions, mapGetters } from 'vuex'
import { changeUserInfo } from '../../api'
const inputs = ['nickname', 'mobile', 'QQ']
export default {
name: 'profile',
data () {
return {
validators: {
nickname: {
origin: '',
valid: true,
validate: (value) => {
if (!value) {
return '请输入昵称'
}
return ''
},
errorMsg: ''
},
mobile: {
origin: '',
valid: true,
validate: (value) => {
if (!value) {
return '请输入手机号码'
} else if (!validatePhone(value)) {
return '手机号码格式错误'
}
return ''
},
errorMsg: ''
},
QQ: {
origin: '',
valid: true,
validate: (value) => {
if (!value) {
return '请输入QQ号'
} else if (!validateQQ(value)) {
return 'QQ号格式错误'
}
return ''
},
errorMsg: ''
}
},
member: {
nickname: '',
mobile: '',
QQ: ''
},
loading: false,
response: {
msg: '',
success: true
},
valid: false
}
},
computed: {
...mapGetters([
'user'
]),
inputErrors () {
return inputs.filter(input => {
return this.validators[input].valid === false
}).map(input => {
return this.validators[input].errorMsg
})
},
hasChange () {
return inputs.filter(input => {
return this.validators[input].origin !== this.member[input]
}).length
}
},
watch: {
'hasChange': function (hasChange) {
this.response.msg = ''
}
},
created () {
this.init()
},
methods: {
init () {
inputs.forEach(input => {
this.member[input] = this.user[input] || ''
this.validators[input].origin = this.user[input] || ''
})
},
...mapActions([
'fetchUser'
]),
validate (value, input) {
this.member[input] = value
const validator = this.validators[input]
if (value === validator.origin) {
validator.valid = true
validator.errorMsg = ''
} else {
const errorMsg = validator.validate(value)
if (errorMsg) {
validator.valid = false
validator.errorMsg = errorMsg
} else {
validator.valid = true
validator.errorMsg = ''
}
}
},
validateAll () {
inputs.forEach(input => {
this.validators[input].validate()
})
return !this.inputErrors.length
},
submit () {
this.response.msg = ''
if (this.validateAll()) {
this.loading = true
changeUserInfo(this.user.id, this.member).then((response) => {
this.loading = false
this.$store.dispatch('fetchUser').then(() => {
this.init()
this.$nextTick(() => {
this.loading = false
this.response.success = true
this.response.msg = '已修改完成'
})
})
}, (response) => {
this.response.success = false
this.response.msg = msgFormatter(response)
})
}
}
},
components: {
Group,
Cell,
XInput,
XButton,
Datetime,
Selector,
Spinner
}
}



